import * as Comlink from "comlink";
import type { PyodideInterface } from "pyodide";
import {
    PyodideFatalErrorReloader,
    type RunnerCallbacks,
    defaultPyodideLoader,
    loadPyodideAndPackage,
    makeRunnerCallback,
    pyodideExpose
} from "pyodide-worker-runner";
import type { ProgramFile, ErrorCallback } from ".";

let attempt = 0;

async function loader(): Promise<PyodideInterface> {
    console.log("pyodide load attempt " + attempt);
    attempt++;
    return await defaultPyodideLoader();
}

const fullLoader = () =>
    loadPyodideAndPackage({ url: URL_BASE + "/python_package.tar.gz.load_by_url", format: "tgz" }, loader);
const reloader = new PyodideFatalErrorReloader(fullLoader);

let programCount = 1;

Comlink.expose({
    run: pyodideExpose(
        async (
            extras,
            code: string,
            inputCallback: RunnerCallbacks["input"],
            outputCallback: RunnerCallbacks["output"],
            errorCallback: ErrorCallback
        ) => {
            const callback = makeRunnerCallback(extras, {
                input: inputCallback,
                output: outputCallback,
                other: (type, data) => type + "-" + JSON.stringify(data)
            });

            return await reloader.withPyodide(async (pyodide) => {
                const runner = pyodide.pyimport("python_runner").PyodideRunner();
                runner.set_callback(callback);
                pyodide.pyimport("builtins").runner = runner;
                await pyodide
                    .pyimport("pyodide_worker_runner")
                    .install_imports(code);

                runner.set_filename(`/my_program_${programCount++}.py` satisfies ProgramFile)

                if (extras.interruptBuffer) pyodide.setInterruptBuffer(extras.interruptBuffer);

                console.log("running code", code);
                console.log(runner);

                try {
                    await runner.run(code);
                    return "success";
                }
                catch (e) {
                    errorCallback(e);
                    return "error";
                }

            });
        }
    )
});